.sendMsgArea {
  width: 42vw;
  height: auto;
  grid-column-start: 2;
  box-sizing: border-box;
  transform: translateX(-1%);
  background-color: blanchedalmond;
  margin: auto;
  padding: 10px;
  margin-top: 20%;
}
.button {
  max-width: 50px;
  align-self: flex-end;
  margin-top: 4px;
  border-radius: 10px;
  border: none;
  background-color: white;
  color: rgb(0, 0, 0);
  width: 120px;
  height: 30px;
  margin-left: 1%;
  
}
.textarea {
  resize: none;
  border-radius: 3px;
  max-width: 200px;
  top: 0;
  height: 80px;
  border: none;
}
