.dialogs{
    background-color: rgb(255, 255, 255);
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 3fr;
}
.chats{

    width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.chat{
    background-color: whitesmoke;
    height: 30px;
    width: 100%;
  
}
.messages{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items:flex-start;
    gap: 10px;
    height: auto;
    padding: 10px;
}
.message{
    min-width: 40%;
    min-height: 13%;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 8px;
    max-width: 80%;
    max-height: 60px;
    background-color: gainsboro;
}